import {
   Checked,
   Fulfill,
   Newspapers,
   Statistic
} from "../../views/index"
export default [
    //积分管理
    {
        path:"/main/integral/checked",
        component:Checked,
        meta:{
            title:"积分上报"
        }
    },
    {
        path:"/main/integral/fulfill",
        component:Fulfill,
        meta:{
            title:"积分审核"
        }
    },
    {
        path:"/main/integral/newspapers",
        component:Newspapers,
        meta:{
            title:"积分统计"
        }
    },
    {
        path:"/main/integral/statistic",
        component:Statistic,
        meta:{
            title:"积分兑现"
        }
    },
    {
        path:"/main/integral",
        redirect:"/main/integral/checked",
        head:"积分管理"
    }
]