import {
  Main,
  Home,
  Login,
  Plan,
  Ytcollege
} from "@/views/index";
import assessmentRouter from "../children/assessmentRouter";
import assistantRouter from "../children/assistantRouter";
import checkRouter from "../children/checkRouter";
import controlRouter from "../children/controlRouter";
import customerRouter from "../children/customerRouter";
import pointRouter from "../children/pointRouter";
const routers = [{
    path: "/",
    redirect: "/main"
  },
  {
    path:"/login",
    component: Login
  },
  {
    path: "/main",
    component: Main,
    children: [{
        path: "/main/home",
        head: "首页",
        component: Home
      },
      ...controlRouter,
      ...checkRouter,
      {
        path: "/main/plan/index",
        head:"工作计划",
        component: Plan
      },
      ...customerRouter,
      {
        path: "/main/college/index",
        head:"亚太学院",
        component: Ytcollege
      },
      ...assessmentRouter,
      ...assistantRouter,
      ...pointRouter,
      {
        path: "/main",
        redirect: "/main/home"
      }
    ]
  }
]
let crumbsArr = []
export const crumbs = (url) =>{
  let filterUrl = routers.filter(item=>item.children)[0].children.find(item=>item.path === url)
  let index = crumbsArr.findIndex(item=>item.path === filterUrl.path)
  if(index === -1 ){
    crumbsArr.push(filterUrl)
    if(crumbsArr.length > 10){
      crumbsArr.shift()
    }
  }
  return crumbsArr
}
export const removeCrumbs = (index) =>{
  crumbsArr.splice(index,1)
  return crumbsArr;
}
export const header = (brr) =>{
  let children = routers.filter(item=>item.children)[0].children;
  let head = children.filter(item=>item.head)
  let headArr = [];
  if(window.localStorage.view){
    let arrView = brr.filter(item=>item.routeIsVisible !== 0); //过滤出与当前相同的权限
    head.forEach(item=>{
        if(item.path){
            arrView.forEach(ite=>{
                if(ite.routerId === item.path.split("/")[2]){
                    headArr.push(item);
                }
            })
        }
  })
}
  return headArr
}
export const aside = (path) => {
  let reg = /\/\w+/g;
  let children = routers.filter(item=>item.children)[0].children;
  let child = children.filter(item=>!item.redirect && item.meta && (item.path.match(reg)[1] === path.match(reg)[1]));
  return child;
}

export default routers;