import {
    Analyze,
    Competitiveness,
    Study,
    Tabulate

} from "../../views/index"
export default [
    //管理助手
    {
        path:"/main/assistant/analyze",
        component:Analyze,
        meta:{
            title:"考勤数据汇总"
        }
    },
    {
        path:"/main/assistant/competitiveness",
        component:Competitiveness,
        meta:{
            title:"客户数据分析"
        }
    },
    {
        path:"/main/assistant/study",
        component:Study,
        meta:{
            title:"主管竞争力排名"
        }
    },
    {
        path:"/main/assistant/tabulate",
        component:Tabulate,
        meta:{
            title:"教室学习排名"
        }
    },
    {
        path:"/main/assistant",
        redirect:"/main/assistant/analyze",
        head:"管理助手"
    }
]