  //员工考勤
import {
    Approvel,
    Statistics,
    Detail,
    Set

} from "../../views/index"
export default [
    {
        path:"/main/checkWork/approvel",
        component:Approvel,
        meta:{
            title:"考勤审批"
        }
    },
    {
        path:"/main/checkWork/statistics",
        component:Statistics,
        meta:{
            title:"考勤统计"
        }
    },
    {
        path:"/main/checkWork/detail",
        component:Detail,
        meta:{
            title:"考勤明细"
        }
    },
    {
        path:"/main/checkWork/set",
        component:Set,
        meta:{
            title:"考勤设置"
        }
    },
    {
        path:"/main/checkWork",
        redirect:"/main/checkWork/approvel",
        head:"员工考勤"
    }
]