import {
    Audit,
    Care,
    DataAnalysis,
    List,
    Record,
    Teacher,
    Vistied
} from "../../views/index"
export default [
    //客户管理
    {
        path:"/main/customer/list",
        component:List,
        meta:{
            title:"客户" ,
            name:"客户列表"
         }
    },
    {
        path:"/main/customer/teacher",
        component:Teacher,
        meta:{
            title:"客户",
            name:"体能老师管理"
         }
    },
    {
        path:"/main/customer/audit",
        component:Audit,
        meta:{
           title:"客户" ,
           name:"客户审核"
        }
    },
    {
        path:"/main/customer/care",
        component:Care,
        meta:{
            title:"客户",
            name:"客户维护"
         }
    },
    {
        path:"/main/customer/dataAnalysis",
        component:DataAnalysis,
        meta:{
            title:"客户",
            name:"客户数据分析"
         }
    },
    {
        path:"/main/customer/vistied",
        component:Vistied,
        meta:{
            title:"客户拜访",
            name:"拜访管理"
         }
    },
    {
        path:"/main/customer/record",
        component:Record,
        meta:{
            title:"客户拜访" ,
            name:"拜访记录"
         }
    },
    {
        path:"/main/customer",
        redirect:"/main/customer/list",
        head:"客户管理"
    }
]