import React from "react";
import {Route,Switch,Redirect} from "react-router-dom";
function Router({routers=[]}){
   let redirectArr = routers.length && routers.filter(item=>item.redirect).map((item,index)=><Redirect key={index} from={item.path} to={item.redirect}></Redirect>)
   return <Switch>
              {
                  routers.length && routers.filter(item=>!item.redirect).map((item,index)=>
                  <Route key={index} path={item.path} render={(props)=>{
                      return <item.component {...props}  children={item.children}/>
                  }}
                  ></Route>).concat(redirectArr)
              }
         </Switch>
}

export default Router;