import { observable, action } from 'mobx';
import {message} from "antd"
import { allList } from "@/api/index";
class allDate {
    @observable allList = []
    @observable size = ""
    // @observable arr=[]
    @action
    all(limit, pageid) {
        allList({
            limit: limit,
            pageid: pageid
        }).then(data => {
            if (!data.data.data.length) { return }
            this.size = data.data.size;
            if (data.data.data.length < limit) { message.info('已到达最大页') }
            this.allList = data.data.data.map(item => {
                item.key = item.id;
                return item;
            })
            window.allList = this.allList
        })
    }
}
export default new allDate;